.loginContainer {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
}
.logoContainer {
  display: flex;
  align-items: center;
  margin: 20px;
}
.logoContainer h1 {
  font-size: 40px;
  margin: 15px;
}
.logoContainer img {
  width: 60px;
  height: 60px;
  border-radius: 3px;
}
.loginForm {
  display: flex;
  flex-direction: column;
  margin-top: 100px;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.loginForm h2 {
  margin: 10px;
}
.loginForm form {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.loginForm form button {
  background-color: #212121;
  padding: 10px 40px;
  font-size: 15px;
  border-radius: 50px;
  outline: none;
  color: white;
  border: solid 2px #81c784;
  cursor: pointer;
  transition: 0.25s;
  margin: 15px;
}
.loginForm form button:hover {
  background-color: #81c784;
}
.inputForm {
  background-color: #212121;
  color: white;
  border: solid 2px #64b5f6;
  padding: 10px;
  text-align: center;
  border-radius: 50px;
  font-size: 20px;
  margin: 10px;
  outline: none;
  transition: 0.25s;
  width: 200px;
}
.inputForm::placeholder {
  color: #7D7D7D;
  font-size: 20px;
}
.inputForm:focus {
  width: 250px;
  border-color: #81c784;
}
